import "./projectscss.css"
import Aos from "aos"
import "aos/dist/aos.css"
import React,{ useEffect } from "react"
import Sidenav from "../sidenav/sidenav";
import Chat from "./chatting.jpg"
import Ecom from "./e-commerce.jpg"
import Todo from "./todo.jpeg"
import { Card} from "react-bootstrap";
import {AiFillGithub} from "@react-icons/all-files/ai/AiFillGithub"
import {BsBoxArrowUpRight} from "@react-icons/all-files/bs/BsBoxArrowUpRight"

function Projects()
{
    useEffect(()=>{
        Aos.init({duration:2000});
    },[]); 

    return(
        <>
            <div id="projectsec" className="pro">
            <Sidenav/>
            <div className="pro1">
                    <div className="wholepro"  data-aos="zoom-in-up" data-aos-duration="800">
                        <h1 className="prohead" data-aos="fade">SOME OF MY <span className="keep">UNIQUE</span> PIECE <br/>OF DEVELOPMENTS</h1>
                    </div>
                    <div className="cards">
                    <div  className="car"> 
                    <Card style={{border:"none"}}>
                        <Card.Img variant="top" src={Todo} style={{width:"100%",height:"70%",borderTopLeftRadius:"10px",borderTopRightRadius:"10px"}}/>
                        <Card.Body>
                            <br/>
                            <Card.Title>Todo App</Card.Title>
                            <Card.Text style={{color:"grey"}} className="text">
                            Make your Todays agenda and schedule your works
                            </Card.Text>
                            <div className="codes">
                                <a href="s" className="links"><BsBoxArrowUpRight/> Demo</a>
                                <a href="s" className="links"><AiFillGithub/> Code</a>
                            </div>
                        </Card.Body>
                    </Card>
                    </div>

                    <div  className="car">
                    <Card style={{border:"none"}}>
                        <Card.Img variant="top" src={Ecom} style={{width:"100%",height:"70%",borderTopLeftRadius:"10px",borderTopRightRadius:"10px"}}/>
                        <Card.Body>
                            <br/>
                            <Card.Title>E-commerce App</Card.Title>
                            <Card.Text style={{color:"grey"}}>
                            Shop your Cosmetics, Dress and Grocery products
                            </Card.Text>
                            <div className="codes">
                                <a href="s" className="links"><BsBoxArrowUpRight/> Demo</a>
                                <a href="s" className="links"><AiFillGithub/> Code</a>
                            </div>
                        </Card.Body>
                    </Card>
                    </div>

                    <div  className="car">
                    <Card style={{border:"none"}}>
                        <Card.Img variant="top" src={Chat} style={{width:"100%",height:"70%",borderTopLeftRadius:"10px",borderTopRightRadius:"10px"}}/>
                        <Card.Body>
                            <br/>
                            <Card.Title>Chatting App</Card.Title>
                            <Card.Text style={{color:"grey"}}>
                            Start chatting with your friends and connect each other
                            </Card.Text>
                            <div className="codes">
                                <a href="s" className="links"><BsBoxArrowUpRight/> Demo</a>
                                <a href="s" className="links"><AiFillGithub/> Code</a>
                            </div>
                        </Card.Body>
                    </Card>
                    </div>


                    </div>
                </div>                    
            </div>
            
        </>
    )
}

export default Projects